#root
	> div
	> div.carousel.slide
	> a.carousel-control-next
	> span.visually-hidden {
	display: none;
}
#root
	> div
	> div.carousel.slide
	> a.carousel-control-prev
	> span.visually-hidden {
	display: none;
}

#root
	> div
	> div.carousel.slide
	> div.carousel-indicators
	> button:nth-child(1) {
	display: none;
}
#root
	> div
	> div.carousel.slide
	> div.carousel-indicators
	> button:nth-child(2) {
	display: none;
}
#root
	> div
	> div.carousel.slide
	> div.carousel-indicators
	> button:nth-child(3) {
	display: none;
}

#enter {
	background-color: #fff;
	border: 1px solid #d5d9d9;
	border-radius: 8px;
	box-shadow: rgba(213, 217, 217, 0.5) 0 2px 5px 0;
	box-sizing: border-box;
	color: #0f1111;
	cursor: pointer;
	display: inline-block;
	font-family: 'Amazon Ember', sans-serif;
	font-size: 13px;
	line-height: 29px;
	padding: 0 3px 0 3px;
	position: relative;
	text-align: center;
	text-decoration: none;
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;
	vertical-align: middle;
	width: 75px;
	font-family: 'Lato', Sans-Serif;
}

#enter:hover {
	background-color: #d8e9e9;
}

#enter:focus {
	border-color: #6a8e7f;
	box-shadow: rgba(213, 217, 217, 0.5) 0 2px 5px 0;
	outline: 0;
}

#carousel {
	width: 800px;
	margin: auto;
	border: 1px solid black;
}

@media screen and (max-width: 620px) {
	#carousel {
		width: 350px;
		margin: auto;
	}
}

@media screen and (min-width: 620px) and (max-width: 800px) {
	#carousel {
		width: 600px;
		margin: auto;
	}
}
